.text {
    &-simple {
        padding: 60px 0;
        background-color: #F1F5F6;
        text-align: center;
        width: fit-content;

        &__zag {
            font-size: 20px;
            line-height: 120%;
            color: #F74B4B;
            margin-bottom: 15px;
        }

        p {
            font-weight: 300;
            font-size: 28px;
            line-height: 40px;
            text-align: center;
            color: #232323;
        }

        .container {
            padding: 0 110px;
        }
    }

    &-video {
        padding: 94px 0 60px;
        border-bottom: 1px solid #D0D7D8;

        .container {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            padding: 0 110px;
        }

        &__text {
            width: 45%;
            padding-right: 70px;

            &-zag {
                font-size: 20px;
                line-height: 120%;
                display: flex;
                align-items: center;
                color: #F74B4B;
                margin-bottom: 25px;

                .sprite-svg {
                    fill: #F74B4B;
                    margin-right: 10px;
                }
            }

            p {
                font-size: 24px;
                line-height: 1.7;

                b {
                    color: #232323;
                }
            }
        }

        &__img {
            width: 55%;

            a {
                width: 100%;

                img {
                    width: 100%;
                }
            }
        }
    }

    &-img {
        padding: 60px 0;

        .container {
            display: flex;
            flex-wrap: wrap;
            align-items: flex-start;
            padding: 0 110px;
        }

        &__img {
            width: 55%;

            img {
                width: 100%;
            }
        }

        &__text {
            width: 45%;
            padding-left: 70px;

            &-zag {
                font-weight: 600;
                font-size: 18px;
                line-height: 30px;
                letter-spacing: 0.08em;
                color: #F74B4B;
                margin-bottom: 15px;
            }

            p {
                font-size: 16px;
                line-height: 25px;
                margin-bottom: 30px;
            }
        }
    }

    &-page {
        h2{
            margin-bottom: 15px;
        }


        h3 {
            margin-bottom: 25px;
        }

        .h3 {
            font-weight: 600;
            font-size: 36px;
            line-height: 122%;
            text-align: center;
            color: #232323;
            margin-bottom: 70px;
        }

        p {
            margin-bottom: 20px;
            font-size: 16px;
            line-height: 170%;
            letter-spacing: -0.02em;
        }

        p.w100 {
            max-width: 100%;
            width: 100%;
        }

        ul {
            margin-bottom: 25px;
            font-size: 16px;

            li {
                position: relative;
                padding-left: 25px;

                &::before {
                    content: '';
                    width: 17px;
                    height: 1px;
                    position: absolute;
                    left: 0;
                    background-color: #F74B4B;
                    top: 12px;
                }
            }
        }

        .ul-disc {
            li {
                &::before {
                    width: 5px;
                    height: 5px;
                    border-radius: 50%;
                }
            }
        }
    }
}

.text-simple.tal {
    text-align: left;

    .h3 {
        font-weight: 600;
        font-size: 36px;
        line-height: 147%;
        margin-bottom: 13px;
    }

    .container {
        padding: 0;
    }

    li {
        font-weight: 300;
        font-size: 36px;
        line-height: 167%;
        color: #000000;
        position: relative;
        padding-left: 60px;

        &::before {
            content: '';
            position: absolute;
            left: 0;
            top: 31px;
            width: 35px;
            height: 1.5px;
            background-color: #000000;
        }
    }
}

.text-img--block {
    padding: 70px 0;

    .h3 {
        font-weight: 600;
        font-size: 36px;
        line-height: 122%;
        text-align: center;
        color: #232323;
        margin-bottom: 70px;
    }

    p {
        float: left;
        margin-bottom: 20px;
        font-size: 16px;
        line-height: 170%;
        letter-spacing: -0.02em;
        max-width: calc(50% - 20px);
    }

    p.w100 {
        max-width: 100%;
        width: 100%;
    }
}

.text-img--block__container {
    width: calc(50% - 50px);
    float: right;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    a {
        width: calc(50% - 10px);
        margin-bottom: 20px;

        &:nth-child(1) {
            width: 100%;
        }
    }

    img {
        border-radius: 10px;
        width: 100%;

        &:nth-child(1) {
            width: 100%;
        }
    }
}

.text-num__list {
    margin: 50px 0;

    .h3 {
        font-weight: 600;
        font-size: 22px;
        line-height: 120%;
        color: #000000;
        margin-bottom: 25px;
    }

    ol {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;

        li {
            width: 45%;
            margin-bottom: 25px;
            color: #F42A2A;
            font-weight: 600;
            font-size: 20px;
            margin-left: 20px;

            span {
                color: #414141;
                font-size: 16px;
                font-weight: 400;
            }
        }
    }
}

.text-img__list {
    margin: 70px 0;

    .h3 {
        font-size: 36px;
        line-height: 122%;
        text-align: center;
        color: #232323;
        margin-bottom: 60px;
    }

    p {
        margin-bottom: 25px;
        font-size: 16px;

    }

    ul {
        margin-bottom: 25px;
        font-size: 16px;

        li {
            position: relative;
            padding-left: 25px;

            &::before {
                content: '';
                width: 17px;
                height: 1px;
                position: absolute;
                left: 0;
                background-color: #F74B4B;
                top: 12px;
            }
        }
    }

    .ul-disc {
        li {
            &::before {
                width: 5px;
                height: 5px;
                border-radius: 50%;
            }
        }
    }

    .img {
        width: 50%;
        margin-left: 40px;

        img {
            width: 100%;
            border-radius: 10px;
        }

        .title {
            font-style: italic;
            font-weight: 500;
            font-size: 14px;
            line-height: 186%;
            color: #414141;
            display: block;
            text-align: center;
            margin-top: 20px;

        }

