    }
}

.small-container {
    padding: 40px 0;
    max-width: 1160px;
    margin: 0 auto;

    .h3 {
        font-size: 36px;
        line-height: 122%;
        text-align: center;
        color: #232323;
        margin-bottom: 20px;
    }

    p {
        font-size: 16px;
        line-height: 162%;
        color: #414141;
        margin-bottom: 20px;
    }

    img {
        width: 100%;
        margin: 35px 0;
        border-radius: 10px;
    }
}

.text-accordion {
    padding: 100px 0;
    display: flex;
    flex-wrap: wrap;
    //align-items: flex-start;
    justify-content: space-between;

    .h2 {
        font-weight: 600;
        font-size: 36px;
        line-height: 122%;
        margin-bottom: 35px;
        color: #000000;
        width: 100%;

        .red {
            color: #F74B4B;
        }
    }

    &__list {
        width: calc(100% - 500px);

        li {
            padding: 20px;
            border-bottom: 1px solid rgba(190, 190, 190, 0.36);

            &:first-child {
                border-top: 1px solid rgba(190, 190, 190, 0.36);
            }

            .zag {
                font-weight: 500;
                font-size: 18px;
                line-height: 139%;
                color: #2B2B2B;
                position: relative;
                transition: .3s;

                &::before {
                    content: '';
                    position: absolute;
                    right: 20px;
                    top: 7px;
                    background-position: center;
                    background-repeat: no-repeat;
                    width: 10px;
                    height: 10px;
                    background-size: contain;
                    background-image: url(../images/svg/ar-down.svg);
                    transition: .3s;
                }
            }

            .text {
                font-size: 16px;
                line-height: 169%;
                color: #4F4F4F;
                margin-top: 13px;
                display: none;
            }
        }

        li.open {
            .zag {
                color: #F74B4B;

                &::before {
                    transform: rotate(-180deg);
                }
            }
        }
    }

    &__info {
        max-width: 450px;
        font-size: 14px;
        line-height: 150%;
        letter-spacing: -0.01em;
        color: #353535;
        background-color: #E9F1F3;
        border-radius: 5px;
        //position: relative;
        padding: 20px 20px 20px 70px;
        background-position: 20px 20px;
        background-repeat: no-repeat;
        position: sticky;
        top: 20px;

        &::before {
            content: '';
            width: 20px;
            height: 20px;
            background-color: #E9F1F3;
            position: absolute;
            left: -10px;
            top: 20px;
            transform: rotate(45deg);
        }
    }
}

.text-accordion__page {
    display: flex;
    padding: 0;

    .text-accordion__content {
        width: calc(100% - 360px);
    }

    .text-accordion__list {
        width: 100%;
        margin-bottom: 60px;
    }

    .text-accordion__fixed {
        padding-top: 75px;
    }

    .text-accordion__info {
        width: 320px;
    }
}


.table-overflow {
    margin: 10px 0;

    table {
        border-collapse: collapse;
        margin: 30px 0;
        min-width: 700px;

        th {
            border: 1px solid rgb(192, 192, 192);
            padding: 10px;
            font-size: 18px;
            background-color: rgb(243, 243, 243);
        }

        td {
            border: 1px solid rgb(192, 192, 192);
            padding: 10px;
            font-size: 16px;
        }
    }
}